img {
  display: block;
}

.cat-info {
  width: 850px;
  text-align: center;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  gap: 25px;
  margin-top: 30px;
  display: flex;
  box-shadow: 2px 2px 3px #9cbdff, -2px -2px 3px #9cbdff;
}

.descr {
  width: 300px;
  flex-direction: column;
  gap: 5px;
  display: flex;
}

.is-hidden {
  display: none;
}

.loader {
  width: 100px;
  height: 100px;
  position: relative;
  top: 50%;
  left: 50%;
}

.loader:before, .loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, .3);
}

.loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #9cbdff;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

